<template>
  <div class="user-management">
    <!--  -->
    <div class="toolbar">
      <el-button 
        type="primary" 
        @click="openAddUserDialog"
        v-if="canManageUsers"
      >
        
      </el-button>
    </div>

    <!--  -->
    <el-table :data="userList" style="width: 100%" v-loading="loading">
      <el-table-column prop="username" label="" />
      <el-table-column prop="realname" label="" />
      <el-table-column label="">
        <template #default="scope">
          <el-tag v-if="scope.row.role.permissions == 255" type="danger"></el-tag>
          <el-tag v-else-if="scope.row.role.permissions == 16" type="primary"></el-tag>
          <el-tag v-else-if="scope.row.role.permissions == 44" type="primary"></el-tag>
          <el-tag v-else-if="scope.row.role.permissions == 62" type="warning"></el-tag>
          <el-tag v-else type="info"></el-tag>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template #default="scope">
          <el-tag :type="scope.row.is_active ? 'success' : 'danger'">
            {{ scope.row.is_active ? '' : '' }}
          </el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="join_time" label="">
        <template #default="scope">
          {{ formatDate(scope.row.join_time) }}
        </template>
      </el-table-column>
      <el-table-column label="" width="250">
        <template #default="scope">
          <el-button
            size="small"
            :type="scope.row.is_active ? 'danger' : 'success'"
            @click="toggleUserStatus(scope.row)"
            v-if="canOperateUser(scope.row)"
          >
            {{ scope.row.is_active ? '' : '' }}
          </el-button>
          
          <el-button
            size="small"
            type="primary"
            @click="openEditRoleDialog(scope.row)"
            v-if="canOperateUser(scope.row)"
          >
            
          </el-button>
          
          <el-button
            size="small"
            type="danger"
            @click="openDeleteDialog(scope.row)"
            v-if="canOperateUser(scope.row)"
          >
            
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--  -->
    <div class="pagination-container">
      <el-pagination
        v-if="total > 0"
        @current-change="handlePageChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total, prev, pager, next"
        :total="total"
      />
    </div>

    <!--  -->
    <el-dialog
      title=""
      v-model="addUserDialogVisible"
      width="500px"
      @close="resetAddUserForm"
    >
      <el-form
        :model="addUserForm"
        :rules="addUserFormRules"
        ref="addUserFormRef"
        label-width="100px"
      >
        <el-form-item label="" prop="username">
          <el-input v-model="addUserForm.username" placeholder="" />
        </el-form-item>
        
        <el-form-item label="" prop="password">
          <el-input 
            v-model="addUserForm.password" 
            type="password"
            placeholder=""
            show-password
          />
        </el-form-item>
        
        <el-form-item label="" prop="realname">
          <el-input v-model="addUserForm.realname" placeholder="" />
        </el-form-item>
        
        <el-form-item label="" prop="role_id">
          <el-select 
            v-model="addUserForm.role_id" 
            placeholder=""
            :disabled="!canSelectRole"
          >
            <el-option 
              v-for="role in availableRoles" 
              :key="role.value" 
              :label="role.label" 
              :value="role.value" 
            />
          </el-select>
        </el-form-item>
      </el-form>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="addUserDialogVisible = false"></el-button>
          <el-button type="primary" @click="submitAddUserForm"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      v-model="editRoleDialogVisible"
      width="500px"
    >
      <el-form
        :model="userRoleForm"
        :rules="roleFormRules"
        ref="userRoleFormRef"
        label-width="100px"
      >
        <el-form-item label="">
          <el-input v-model="userRoleForm.username" disabled />
        </el-form-item>
        
        <el-form-item label="" prop="role">
          <el-select v-model="userRoleForm.role" placeholder="">
            <el-option label="" value="webuser" />
            <el-option label="" value="tooluser" />
            <el-option label="" value="announceuser" />
            <el-option label="" value="user" />
          </el-select>
        </el-form-item>
      </el-form>
      
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="editRoleDialogVisible = false"></el-button>
          <el-button type="primary" @click="submitRoleForm"></el-button>
        </span>
      </template>
    </el-dialog>

    <!--  -->
    <el-dialog title="" v-model="deleteUserVisible" width="300px" center>
      <span></span>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="deleteUserVisible = false"></el-button>
          <el-button type="danger" @click="confirmDelete"></el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "UserManagement",
  data() {
    return {
      loading: false,
      userList: [],
      total: 0,
      currentPage: 1,
      pageSize: 10,
      deleteUserVisible: false,
      editRoleDialogVisible: false,
      addUserDialogVisible: false,
      currentUser: null,
      userRoleForm: {
        username: "",
        role: ""
      },
      addUserForm: {
        username: "",
        password: "111111",
        realname: "",
        role_id: 1
      },
      roleFormRules: {
        role: [{ required: true, message: "", trigger: "change" }]
      },
      addUserFormRules: {
        username: [{ required: true, message: "", trigger: "blur" }],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, message: "6", trigger: "blur" }
        ],
        realname: [{ required: true, message: "", trigger: "blur" }],
        role_id: [{ required: true, message: "", trigger: "change" }]
      }
    };
  },
  computed: {
    // 
    loggedInUser() {
      return this.$auth.user;
    },
    
    // 
    canManageUsers() {
      if (!this.loggedInUser) return false;
      
      // 
      if (this.loggedInUser.role && this.loggedInUser.role.name === "") {
        return true;
      }
      
      // 
      if (this.loggedInUser.role && this.loggedInUser.role.name === "") {
        return true;
      }
      
      return false;
    },

    // 
    availableRoles() {
      const roles = [];
      
      // 
      roles.push({ label: "", value: 5 });
      
      // 
      if (this.loggedInUser.role && this.loggedInUser.role.name === "") {
        roles.push({ label: "", value: 3 }); 
        roles.push({ label: "", value: 2 });
        return roles;
      }
      
      // 
      if (this.loggedInUser.role && this.loggedInUser.role.name === "") {
        roles.push({ label: "", value: 3 }); 
        roles.push({ label: "", value: 2 });
        roles.push({ label: "", value: 4 });
        // 
        return roles;
      }
      
      return roles;
    },

    // 
    canSelectRole() {
      return this.availableRoles.length > 1 || 
             (this.availableRoles.length === 1 && this.addUserForm.role_id !== this.availableRoles[0].value);
    }
  },
  mounted() {
    this.loadUserList(1);
  },
  methods: {
    // 
    async loadUserList(page) {
      this.loading = true;
      try {
        const response = await this.$http.getUserList(page);
        if (response.code === 200) {
            if (response.data.user_list.length > 0) {
              this.userList = response.data.user_list;
              this.total = response.data.total_count
            } else {
              this.userList = [];
              this.total = 0
            }
        } else {
          this.$message.error(response.message || "");
        }
      } catch (error) {
        this.$message.error("");
      } finally {
        this.loading = false;
      }
    },

    // 
    handlePageChange(page) {
      this.currentPage = page;
      this.loadUserList(page);
    },

    // 
    canOperateUser(user) {
      // 
      if (!this.canManageUsers) {
        return false;
      }

      // 
      if (this.loggedInUser.role.name === "" || this.loggedInUser.role.name === "" || this.loggedInUser.role.name === "" ) {
        return false;
      }
      
      // 
      if (this.loggedInUser.role.name === "") {
        if (this.loggedInUser.id === user.id) {
          return false;
        }
      }
      
      // 
      if (this.loggedInUser.role.name === "") {
        // 
        if (user.role.name === "" || user.role.name === "") {
          return false;
        }
      }
      
      return true;
    },

    // 
    async toggleUserStatus(user) {
      // 
      if (!this.canOperateUser(user)) {
        this.$message.error("");
        return;
      }
      
      try {
        const newStatus = user.is_active ? 0 : 1;
        const response = await this.$http.activeUser(user.id, newStatus);
        if (response.code === 200) {
          this.$message.success(`${!user.is_active ? '' : ''}`);
          this.loadUserList();
        } else {
          this.$message.error(response.message || `${!user.is_active ? '' : ''}`);
        }
      } catch (error) {
        this.$message.error("");
      }
    },

    // 
    openAddUserDialog() {
      this.addUserDialogVisible = true;
      // 
      if (this.availableRoles.length > 0) {
        this.addUserForm.role_id = this.availableRoles[0].value;
      }
    },

    // 
    resetAddUserForm() {
      this.addUserForm = {
        username: "",
        password: "111111",
        realname: "",
        role_id: this.availableRoles.length > 0 ? this.availableRoles[0].value : 1
      };
      if (this.$refs.addUserFormRef) {
        this.$refs.addUserFormRef.resetFields();
      }
    },

    // 
    submitAddUserForm() {
      this.$refs.addUserFormRef.validate(async (valid) => {
        if (valid) {
          // 
          const isValidRole = this.availableRoles.some(role => role.value === this.addUserForm.role_id);
          if (!isValidRole) {
            this.$message.error("");
            return;
          }
          
          try {
            const response = await this.$http.addUser(this.addUserForm);
            if (response.code === 200) {
              this.$message.success("");
              this.addUserDialogVisible = false;
              this.resetAddUserForm();
              this.loadUserList();
            } else {
              this.$message.error(response.message || "");
            }
          } catch (error) {
            this.$message.error("");
          }
        }
      });
    },

    // 
    openEditRoleDialog(user) {
      // 
      if (!this.canOperateUser(user)) {
        this.$message.error("");
        return;
      }
      
      this.currentUser = user;
      this.userRoleForm.username = user.username;
      // 
      this.userRoleForm.role = "";
      this.editRoleDialogVisible = true;
    },

    // 
    submitRoleForm() {
      this.$refs.userRoleFormRef.validate(async (valid) => {
        if (valid) {
          try {
            let roleData = {
              id: this.currentUser.id
            };
            
            // 
            switch (this.userRoleForm.role) {
              case "superuser":
                roleData.role_id = 1;
                roleData.permissions = 255;
                break;
              case "webuser":
                roleData.role_id = 4;
                roleData.permissions = 62;
                break;
              case "tooluser":
                roleData.role_id = 3;
                roleData.permissions = 44;
                break;
              case "announceuser":
                roleData.role_id = 2;
                roleData.permissions = 16;
                break;
              case "user":
                roleData.role_id = 5;
                roleData.permissions = 1;
                break;
            }
            
            const response = await this.$http.updateUserRole(roleData);
            if (response.code === 200) {
              this.$message.success("");
              this.editRoleDialogVisible = false;
              this.loadUserList(1);
            } else {
              this.$message.error(response.message || "");
            }
          } catch (error) {
            this.$message.error("");
          }
        }
      });
    },

    // 
    openDeleteDialog(user) {
      // 
      if (!this.canOperateUser(user)) {
        this.$message.error("");
        return;
      }
      
      this.currentUser = user;
      this.deleteUserVisible = true;
    },

    // 
    async confirmDelete() {
      try {
        const response = await this.$http.deleteUser(this.currentUser.id);
        if (response.code === 200) {
          this.$message.success("");
          this.deleteUserVisible = false;
          this.loadUserList();
        } else {
          this.$message.error(response.message || "");
        }
      } catch (error) {
        this.$message.error("");
      }
    },

    // 
    formatDate(dateString) {
      if (!dateString) return "";
      const date = new Date(dateString);
      return date.toLocaleDateString() + " " + date.toLocaleTimeString();
    }
  }
};
</script>

<style scoped>
.toolbar {
  margin-bottom: 20px;
}

.pagination-container {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.dialog-footer {
  display: flex;
  justify-content: flex-end;
}
</style>